    justify-content: space-between;
  }
}
.main-nav__container--open {
  background-color: #283645;
  width: 100%;
}
.main-nav__container--closed .main-nav__logo {
  position: relative;
  box-shadow: none;
  background-color: transparent;
}
.main-nav__container--open .main-nav__logo {
  box-shadow: inset 0 -1px 0 #000000;
}
.main-nav__container--nojs {
  background-color: #283645;
}
@media (min-width: 1200px) {
