}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: absolute;
  width: 100%;
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    position: relative;
    width: 720px;
  }
}
.main-nav__list--nojs {
  position: relative;
}
.main-nav__item {
  text-align: center;
  box-shadow: inset 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
  background-color: #283645;
}
.main-nav__item:hover .main-nav__link {
  color: #d22856;
}
.main-nav__item:active .main-nav__link {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1200px) {
  .main-nav__item {
    box-shadow: none;
    padding: 0;
    margin-left: 80px;
    background-color: transparent;
  }
  .main-nav__item:active .main-nav__link {
    color: rgba(255, 255, 255, 0.3);
  }
}
.main-nav__link {
  text-decoration: none;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  display: block;
  padding: 16px 20px;
}
@media (min-width: 1200px) {
  .main-nav__link {
    text-transform: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    padding: 46px 0;
    position: relative;
  }
  .main-nav__link--current::after {
    content: "";
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: #ffffff;
    left: 0;
    right: 0;
    bottom: 34px;
  }
}
.main-nav__link-html--uppercase {
  text-transform: uppercase;
}
.main-nav__toggle {
  padding: 0;
  border: none;
  width: 50px;
  height: 24px;
  background-color: transparent;
  fill: #ffffff;
  display: block;
  position: absolute;
  right: 20px;
  top: 22px;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    margin-top: 20px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle--nojs {
  display: none;
}
.main-nav__switch--closed .main-nav__toggle::after {
  content: "";
  background-color: #ffffff;
  width: 50px;
  height: 4px;
  border-radius: 1px;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav__switch--closed .main-nav__toggle:hover::after {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav__switch--closed .main-nav__toggle:active::after {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  opacity: 0.3;
}
.main-nav__switch--closed .main-nav__item {
  display: none;
}
@media (min-width: 1200px) {
  .main-nav__switch--closed .main-nav__item {
    display: list-item;
  }
}
.main-nav__switch--open .main-nav__toggle::after,
.main-nav__switch--open .main-nav__toggle::before {
  content: "";
  background-color: #ffffff;
  width: 30px;
  height: 3px;
  position: absolute;
  top: 10px;
  right: -4px;
}
.main-nav__switch--open .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__switch--open .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav__switch--open .main-nav__toggle:hover::after,
.main-nav__switch--open .main-nav__toggle:hover::before {
  background-color: #d22856;
}
.main-nav__switch--open .main-nav__toggle:active::after,
.main-nav__switch--open .main-nav__toggle:active::before {
  background-color: #d22856;
  opacity: 0.3;
}
.main-nav__switch--nojs .main-nav__item {
  display: list-item;
}
